export class RoomClient {
	constructor(roomId) {
		this.id = roomId;

		this.state = {
			peers: [],
			peersInCall: [],

			calls: {},
			mediaStream: null
		};

		this.boards = {};
		this.bus = new EventBus();
		
		this.peerId = peerId;
		this.peer = new Peer(peerId, {
			config: {
				'iceServers': [
					{ url: 'stun:stun.l.google.com:19302' },
		// 			{
		//     url: 'turn:numb.viagenie.ca',
		//     credential: 'muazkh',
		//     username: 'webrtc@live.com'
		// }
		  		]
		  	}
		});

		this.peer.on('open', function(id) {
			console.log('My peer ID is: ' + id);
		});
		this.peer.on('call', (connection) => {
			// Answer call with video stream
			connection.answer(this.state.mediaStream);

			this._setupCall(connection);
			this._emitStateChange();
		});

		// this.peer.on('connect', (dataConnection) => {
		// 	// No metadata, no dataconnection
		// 	if (!dataConnection.metadata) {
		// 		dataConnection.close();
		// 		return;
		// 	}

			
		// })

		this.socket = io(API_URL, {
			query: {
				roomId: roomId,
				peerId: peerId
			}
		}); //io('http://lukas-imac.local:8080');

		this.socket.on('connect', () => {
			console.log('connected to server');
		});

		this.socket.on('disconnect', () => {
			console.log('disconnected from server');
		});

		this.socket.on('peers-update', (data) => this._onPeersUpdate(data.peers));
		this.socket.on('peers-in-call-update', (data) => this._onPeersInCallUpdate(data.peers));
	}

	_onPeersUpdate(peers) {
		this.state.peers = peers;
		this._emitStateChange();
	}

	_onPeersInCallUpdate(peersInCall) {
		if (peersInCall != this.state.peers) {
			if (this.state.joinedCall) {
				const newPeers = peersInCall.filter(peerId => !this.state.peersInCall.includes(peerId));
				const removedPeers = this.state.peersInCall.filter(peerId => !peersInCall.includes(peerId));

				for (const removedPeer of removedPeers) {
					const call = this.state.calls[removedPeer];

					if (!call)
						continue;

					call.connection.close();

					delete this.state.calls[removedPeer];
				}

				for (const newPeer of newPeers) {
					if (!newPeer) {
						continue;
					}

					const connection = this.peer.call(newPeer, this.state.mediaStream);
					this._setupCall(connection);
				}
			}

			this.state.peersInCall = peersInCall;

			this._emitStateChange();
		}
	}

	_setupCall(peerConnection) {
		const remotePeerId = peerConnection.peer;

		peerConnection.on('stream', stream => {
			this.state.calls[remotePeerId].stream = stream;
			this._emitStateChange();
		});

		peerConnection.on('error', error => console.error('error in peer', error));
		peerConnection.on('close', () => {
			delete this.state.calls[remotePeerId];
			this._emitStateChange();
		});

		this.state.calls[remotePeerId] = {
			connection: peerConnection,
			stream: null
		};
	}

	_emitStateChange() {
		this.bus.emit('state-changed', this.state);
	}

	async joinCall() {
		if (this.state.peersInCall.includes(this.peerId))
			return;

		this.state.mediaStream = await getMediaStream();

		for (const peerId of this.state.peersInCall) {
			const connection = this.peer.call(peerId, this.state.mediaStream);
			this._setupCall(connection);
		}

		this.socket.emit('join-call');
		this._emitStateChange();
	}

	leaveCall() {
		this.state.mediaStream = null;

		for (const call of this.state.calls) {
			if (!call)
				continue;

			call.connection.close();
		}

		this.socket.emit('leave-call');
		this.state.calls = {};
		this._emitStateChange();
	}

	onStateUpdate(callback) {
		this.bus.on('state-changed', callback);

		return () => {
			this.bus.off('state-changed', callback);
		}
	}

	disconnect() {
		// TODO make this work properly
		this.peer.destroy();
		this.socket.close();
	}
}