<template>
	<div v-if="visible" class="bg-gray-900 rounded w-auto p-5 pt-0 mb-5 text-gray-100 font-mono text-left` shadow">
		<h3 class="text-xs text-gray-500 text-center py-2">Debug</h3>
		<p v-for="(value, key) in data" :key="key" class="text-left mb-2"><b class="block text-xs text-gray-500">{{ key }}:</b> <pre class="inline-block">{{ value }}</pre></p>
	</div>
</template>
<script type="text/javascript">
export default {
	props: ['data'],
	data: () => ({
		visible: process.env.NODE_ENV !== 'production'
	}),
	created() {
		window.addEventListener('keypress', this.onKeypress);
	},
	destroyed() {
		window.removeEventListener('keypress', this.onKeypress);
	},
	methods: {
		onKeypress(e) {
			if(e.charCode === 96) {
				this.visible = !this.visible;
			}
		}
	}
}
</script>