<template>
	<main class="h-screen">
		<div class="absolute bg-gray-800 rounded w-full h-16 flex items-center content-between p-5 border-b-2 border-gray-900 top-0">
			<h1 class="text-left font-mono text-3xl text-gray-100 font-bold">🍻 SAUF.CHAT</h1>
			<h2 class="flex-1 text-center font-mono text-xl text-gray-100">Room: {{ $route.params.roomId }}</h2>
			<router-link class="bg-gray-700 p-2 rounded text-gray-400 text-xs" to="/">Leave Room</router-link>
		</div>
		<section class="flex mt-16 h-screen">
			<div v-if="!joinedCall" class="w-full bg-gray-800 flex justify-center items-center">
				<section>
					<h1 class="text-3xl text-gray-500 mb-5">{{ peopleInCallMessage }}</h1>
					<button class="text-3xl text-gray-500 bg-gray-900 p-5 rounded-lg" @click="joinCall">Join Call</button>
				</section>
			</div>
			<div v-else class="w-full bg-gray-800 overflow-y-scroll" :class="{ 'w-1/3 max-w-md': activeBoard !== null }">
				<div class="flex flex-wrap pt-5">
					
					<video-feed class="bg-gray-900 rounded-lg shadow w-1/3 overflow-hidden mb-5 mx-5 relative" :class="{ 'w-full max-w-md': activeBoard !== null }" peer-id="You" :stream="mediaStream"></video-feed>

					<video-feed class="bg-gray-900 rounded-lg shadow w-1/3 overflow-hidden mb-5 mx-5 relative" :class="{ 'w-full max-w-md': activeBoard !== null }" peer-id="You" :stream="mediaStream"></video-feed>

					<video-feed class="bg-gray-900 rounded-lg shadow w-1/3 overflow-hidden mb-5 mx-5 relative" :class="{ 'w-full max-w-md': activeBoard !== null }" v-for="(stream, peerId) in calls" :key="peerId" :peer-id="peerId" :stream="stream"></video-feed>
				</div>
				<div class="text-gray-100 bg-gray-900 rounded-full p-1 absolute font-bold" style="top: 50%; right: -3px;">:</div>
			</div>

			<div v-if="activeBoard !== null" class="w-full bg-gray-900 p-5">
				<spotify-board v-show="activeBoard === 'spotify'" :board="boards.spotify"></spotify-board>
			</div>
		</section>


		<!-- <div id="nav">
			<router-link to="/">Home</router-link> |
			<router-link to="/about">About</router-link>
		</div>
		<router-view /> -->
		<!-- <div class="bg-gray-700 bg-gray-800 fixed bottom-0 right-0 flex items-center justify-end rounded-lg m-5 overflow-hidden shadow-lg border-2 border-indigo-500">
			<button @click="activeBoard = null" class="bg-gray-800 text-gray-500 px-5 py-2" :class="{ 'bg-indigo-500 text-indigo-100': !activeBoard }">People</button>
			<button @click="activeBoard = 'spotify'" class="bg-gray-800 text-gray-500 px-5 py-2" :class="{ 'bg-indigo-500 text-indigo-100': activeBoard === 'spotify' }">Spotify</button>
		</div> -->
		<debug-box class="fixed right-0 top-0 m-10" :data="{ PeerID: peerId, RoomID: $route.params.roomId, Peers: peers, PeersInCall: peersInCall, Calls: calls, JoinedCall: joinedCall }"></debug-box>
	</main>
</template>
<script type="text/javascript">

import { RoomClient, getSocket } from '@/socket';
import localPeerId from '@/peer-id';

import VideoFeed from '@/components/VideoFeed';
import DebugBox from '@/components/DebugBox';
import SpotifyBoard from '@/components/SpotifyBoard';


export default {
	data: () => ({
		peers: [],
		peersInCall: [],
		calls: {},
		boards: {},
		activeBoard: null,
		mediaStream: null,
		inSetup: false
	}),
	updated() {
		if(!this.room || this.room.id !== this.$route.params.roomId) {
			this.setup(this.$route.params.roomId);
		}
	},
	created() {
		console.log('created');
		this.setup(this.$route.params.roomId);
	},
	computed: {
		peerId() {
			return localPeerId;
		},
		peopleInCallMessage() {
			if (this.peers.length === 1) {
				return `There is currently 1 person online and ${this.peersInCall.length} video-calling.`;
			}

			return `There are currently ${this.peers.length} people online and ${this.peersInCall.length} video-calling.`;
		},
		joinedCall() {
			return this.peersInCall.includes(localPeerId);
		}
	},
	methods: {
		async setup(roomId) {
			if (this.inSetup)
				return;

			this.inSetup = true;
			this.peers = [];
			this.calls = {};

			try {
				this.room = new RoomClient(roomId);

				this.room.onStateUpdate((state) => {
					this.peers = state.peers;
					this.peersInCall = state.peersInCall;

					// We have to remove the connection object from the calls in order for there not to be circular references.
					let filteredCalls = {};
					for (const remotePeerId in state.calls) {
						if (remotePeerId === localPeerId) {
							continue;
						}

						filteredCalls[remotePeerId] = state.calls[remotePeerId].stream;
					}
					this.calls = filteredCalls;
					this.mediaStream = state.mediaStream;
				});

				// this.room.onBoardsChanged((boards) => {
				// 	this.boards = boards;
				// });

				this.inSetup = false;
			} catch (e) {
				console.log('Error occurred', e);
			}
		},
		joinCall() {
			if (this.room) {
				this.room.joinCall();
			}
		}
	},
	beforeRouteLeave(to, from, next) {
		if (confirm('Do you really want to leave this room?')) {
			if (this.room)
				this.room.disconnect();

			next();
		} else {
			next(false);
		}
	},
	components: {
		VideoFeed,
		DebugBox,
		SpotifyBoard
	}
}

</script>
