<template>
	<div>
		<video ref="video" :srcObject.prop="stream" autoplay="true" muted="true" />
		<div class="absolute bottom-0 p-2 bg-gray-800 w-full opacity-50 text-gray-100 flex items-center justify-between">
			<div class="flex">
				<button @click="muted = !muted">{{ muted ? '🔇' : '🔈' }}</button>
				<input class="block bg-red-500 w-24 bg-red-500" type="range" min="0" max="2" step="0.01" v-model="volume" name="volume">
			</div>
			<span class="flex-1 text-xs text-right">{{ peerId }}</span>
		</div>
	</div>
</template>
<script type="text/javascript">

export default {
	props: ['peerId', 'stream'],
	data: () => ({
		muted: process.env.NODE_ENV !== 'production',
		volume: 1,
		showSettings: false
	}),
	mounted() {
		console.log(this.$refs.video)
		// create an audio context and hook up the video element as the source
		this.audioCtx = new AudioContext();
		var source = this.audioCtx.createMediaElementSource(this.$refs.video);

		// create a gain node
		this.gainNode = this.audioCtx.createGain();
		this.gainNode.gain.value = this.muted ? 0 : this.volume; // double the volume
		source.connect(this.gainNode);

		// connect the gain node to an output destination
		this.gainNode.connect(this.audioCtx.destination);
	},
	watch: {
		volume() {
			if(this.gainNode)
				this.gainNode.gain.value = this.muted ? 0 : this.volume;
		},
		muted() {
			if(this.gainNode)
				this.gainNode.gain.value = this.muted ? 0 : this.volume;
		}
	}
}
</script>